}

.datainfo {
  color: #b3246d;
  font-size: 20px;
}

.chart-row {
  margin-bottom: 15px;
}
.inline-chart {
  display: inline-block;
  width: 48%;
  margin-left: 1%;
}

h1 {
  color: rgb(63, 81, 181);
  font-weight: bold;
}
.grid-container {
  display: grid;
  grid-template-columns: 0.3fr 2fr 1.2fr 1fr;
  grid-template-rows: 0.4fr 2.2fr 0.4fr;
  gap: 0px 0px;
  grid-template-areas: "topPanel topPanel topPanel topPanel" "lsidePanel mapArea mapArea rsidePanel" "lsidePanel bottomPanel bottomPanel bottomPanel";
}

.topPanel {
  grid-area: topPanel;
  background-color: lightgrey;
  padding-left: 20px;
}

.mapArea {
  grid-area: mapArea;
  background-color: #008080;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 100%;
}

.lsidePanel {
  grid-area: lsidePanel;
  background-color: black;
}

.rsidePanel {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas: "rsidetop rsidetop rsidetop" "rsidedown rsidedown rsidedown";
  grid-area: rsidePanel;
  padding-left: 5px;
  background-color: black;
}

.rsidetop {
  grid-area: rsidetop;
  background-color: lightgrey;
  border-style: solid;
  border-color: black;
  border-radius: 20px;
}

.rsidedown {
  grid-area: rsidedown;

  background-color: lightgrey;
  border-style: solid;
  border-color: black;
  border-radius: 20px;
}

.bottomPanel {
  grid-area: bottomPanel;
  background-color: black;

  padding-left: 150px;
}
